import { FunctionComponent, VNode } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/CartSummary/CartSummary.css';
import {
  Accordion,
  AccordionSection,
  Card,
  Divider,
} from '@adobe/elsie/components';
import { Text, useText } from '@adobe/elsie/i18n';
import { CartSummaryItem, Heading } from '@/checkout/components';
import { CartSummarySkeleton } from '@/checkout/components/CartSummary/CartSummarySkeleton';
import { MobileCartSummarySkeleton } from '@/checkout/components/CartSummary/MobileCartSummarySkeleton';
import { useBreakpoint } from '@/checkout/hooks/useBreakpoint';
import { Item } from '@/checkout/data/models';

export type CartSummaryActionProps = {
  type: 'view-more' | 'view-all';
  onClick: () => void;
};

export interface CartSummaryProps
  extends Omit<HTMLAttributes<HTMLDivElement>, 'action'> {
  action?: VNode<CartSummaryActionProps>;
  taxIncluded: boolean;
  isLoading: boolean;
  items: Item[];
  routeCart?: () => string;
  totalQuantity: number;
}

export const CartSummaryAction: FunctionComponent<CartSummaryActionProps> = ({
  type,
  onClick,
  children,
}) => {
  return (
    <>
      <Divider
        className="checkout-cart-summary__divider-bottom"
        variant="primary"
      />
      <button
        data-testid={`action-${type}`}
        className="checkout-cart-summary__action"
        onClick={onClick}
      >
        {children}
      </button>
    </>
  );
};

const Mobile: FunctionComponent<CartSummaryProps> = ({
  title,
  isLoading,
  totalQuantity,
  routeCart,
  items,
  taxIncluded,
  action,
}) => {
  return (
    <>
      {isLoading ? (
        <MobileCartSummarySkeleton />
      ) : (
        <Accordion actionIconPosition="left">
          <AccordionSection
            title={`${title} (${totalQuantity})`}
            secondaryText={
              routeCart?.() && (
                <a href={routeCart?.()} rel="noreferrer">
                  <Text id="Checkout.CartSummary.editCart" />
                </a>
              )
            }
          >
            {items?.map((item) => (
              <CartSummaryItem
                key={item?.uid}
                item={item}
                taxIncluded={taxIncluded}
              />
            ))}

            {action}
          </AccordionSection>
        </Accordion>
      )}
    </>
  );
};

const Desktop: FunctionComponent<CartSummaryProps> = ({
  title,
  isLoading,
  totalQuantity,
  routeCart,
  items,
  action,
  taxIncluded,
}) => {
  return (
    <>
      {isLoading ? (
        <CartSummarySkeleton />
      ) : (
        <Card>
          <div className="checkout-cart-summary__heading">
            <Heading level={2} className="checkout-cart-summary__title">
              {title} ({totalQuantity})
            </Heading>

            {routeCart?.() && (
              <a
                className="checkout-cart-summary__edit"
                href={routeCart?.()}
                rel="noreferrer"
              >
                <Text id="Checkout.CartSummary.editCart" />
              </a>
            )}
          </div>

          <Divider
            className="checkout-cart-summary__divider-top"
            variant="primary"
          />

          <div className="checkout-cart-summary__items">
            {items?.map((item) => (
              <CartSummaryItem
                key={item?.uid}
                item={item}
                taxIncluded={taxIncluded}
              />
            ))}
          </div>

          {action}
        </Card>
      )}
    </>
  );
};

export const CartSummary: FunctionComponent<CartSummaryProps> = ({
  className,
  ...props
}) => {
  const title = useText('Checkout.CartSummary.title').title;
  const breakpoint = useBreakpoint();

  return (
    <div className={classes(['checkout-cart-summary', className])}>
      {breakpoint === 'small' ? (
        <Mobile title={title} {...props} />
      ) : (
        <Desktop title={title} {...props} />
      )}
    </div>
  );
};
