import { FunctionComponent } from 'preact';
import { HTMLAttributes } from 'preact/compat';
import { classes } from '@adobe/elsie/lib';
import '@/checkout/components/CartSummaryItem/CartSummaryItem.css';
import { WarningWithCircle } from '@adobe/elsie/icons';
import { CartItem, Icon, Image, Price } from '@adobe/elsie/components';
import { Item, StockStatus } from '@/checkout/data/models';
import { useText, Text } from '@adobe/elsie/i18n';

export interface CartSummaryItemProps extends HTMLAttributes<HTMLDivElement> {
  item: Item;
  taxIncluded?: boolean;
}

export const CartSummaryItem: FunctionComponent<CartSummaryItemProps> = ({
  className,
  item,
  taxIncluded = false,
}) => {
  const translations = useText({
    Sender: 'Checkout.CartSummary.GiftCard.sender',
    Recipient: 'Checkout.CartSummary.GiftCard.recipient',
    Message: 'Checkout.CartSummary.GiftCard.message',
    Download: 'Checkout.CartSummary.Downloadable.download',
    OutOfStockAlert: 'Checkout.OutOfStock.alert',
    ...('links' in item && item.links
      ? {
          Files: (
            <Text
              id="Checkout.CartSummary.Downloadable.files"
              plural={item.links.length}
              fields={{
                count: item.links.length,
              }}
            />
          ),
        }
      : {}),
  });

  // Taking advantage of using withText() to build the string literal for translations.Files
  // https://github.com/synacor/preact-i18n/tree/2.3.1-preactx?tab=readme-ov-file#examples-4

  let price = item.regularPrice;
  let total = item.total;

  if (taxIncluded) {
    price = item.priceInclTax;
    total = item.totalInclTax;
  }

  const configurations = {
    ...('customizableOptions' in item ? item.customizableOptions : {}),
    ...('configurableOptions' in item ? item.configurableOptions : {}),
    ...('links' in item && item.links
      ? { [translations.Files]: item.links.join(', ') }
      : {}),
    ...('senderName' in item && item.senderName
      ? { [translations.Sender]: item.senderName }
      : {}),
    ...('recipientName' in item && item.recipientName
      ? { [translations.Recipient]: item.recipientName }
      : {}),
    ...('senderEmail' in item &&
    'senderName' in item &&
    item.senderName &&
    item.senderEmail
      ? { [translations.Sender]: `${item.senderName} (${item.senderEmail})` }
      : {}),
    ...('recipientEmail' in item &&
    'recipientName' in item &&
    item.recipientName &&
    item.recipientEmail
      ? {
          [translations.Recipient]: `${item.recipientName} (${item.recipientEmail})`,
        }
      : {}),
    ...('message' in item && item.message && item.message != ''
      ? { [translations.Message]: truncateMessage(item.message as string, 60) }
      : {}),
  };

  function truncateMessage(message: string, limit: number) {
    if (message.length > limit) {
      return `${message.slice(0, limit)}...`;
    }
    return message;
  }

  const alert =
    item.stockStatus === StockStatus.OutOfStock ? (
      <span data-testid="checkout-cart-item-alert">
        <Icon source={WarningWithCircle} size="16" />
        {translations.OutOfStockAlert}
      </span>
    ) : undefined;

  const lowInventory =
    !alert && item.onlyXLeftInStock ? (
      <span data-testid="checkout-cart-item-low-inventory">
        <Icon source={WarningWithCircle} size="16" />
        <Text
          id="Checkout.OutOfStock.lowInventory"
          plural={item.onlyXLeftInStock}
          fields={{
            count: item.onlyXLeftInStock,
          }}
        />
      </span>
    ) : undefined;

  return (
    <div className={classes(['checkout-cart-summary-item', className])}>
      <CartItem
        data-testid="cart-summary-item"
        key={item.uid}
        taxIncluded={taxIncluded}
        title={<div data-testid="product-name">{item.name}</div>}
        sku={<div>{item.sku}</div>}
        quantity={item.quantity}
        image={
          <Image
            src={item.image?.src ?? ''}
            alt={item.image?.alt}
            loading="lazy"
            width="80"
            height="80"
            params={{ width: 80 }}
          />
        }
        warning={lowInventory}
        alert={alert}
        configurations={configurations}
        price={
          <Price
            amount={item.regularPrice.value}
            currency={price.currency}
            weight="normal"
          />
        }
        total={
          <>
            <Price
              amount={item.regularPrice.value * item.quantity}
              currency={price.currency}
              variant={item.discounted ? 'strikethrough' : 'default'}
            />

            {item.discounted && (
              <Price amount={total.value} currency={total.currency} sale />
            )}
          </>
        }
      />
    </div>
  );
};
