import {
  CartSummaryAction,
  CartSummary as CartSummaryComponent,
} from '@/checkout/components/CartSummary/CartSummary';
import { DEFAULT_VISIBLE_ITEMS } from '@/checkout/containers/CartSummary/constants';
import { useStore } from '@/checkout/context/store';
import { Item, PriceDisplay, TotalDisplay } from '@/checkout/data/models';
import { redirect } from '@/checkout/lib';
import { cartSignal } from '@/checkout/signals';
import { useText } from '@adobe/elsie/i18n';
import { Container } from '@adobe/elsie/lib';
import { HTMLAttributes, useCallback, useMemo, useState } from 'preact/compat';

export interface CartSummaryProps extends HTMLAttributes<HTMLDivElement> {
  routeCart?: () => string;
}

type CartSummaryState = {
  canDisplayMoreItems: boolean;
  hasHiddenItems: boolean;
  isLoading: boolean;
  isTaxIncluded: boolean;
  totalQuantity: number;
  visibleItems: Item[];
};

const initialState: CartSummaryState = {
  canDisplayMoreItems: false,
  hasHiddenItems: false,
  isLoading: true,
  isTaxIncluded: false,
  totalQuantity: 0,
  visibleItems: [],
};

export function useCartSummary(displayMaxItems: boolean): CartSummaryState {
  const { config } = useStore();
  const isLoadingStoreConfig = config === undefined;

  const cartData = cartSignal.value.data;
  const isLoadingCartData = cartData === undefined || cartData === null;

  if (isLoadingStoreConfig || isLoadingCartData) return initialState;

  const { cartSummaryMaxItems, cartSummaryTotalDisplay, taxCartDisplay } =
    config;

  const useQtys = cartSummaryTotalDisplay === TotalDisplay.Quantity;
  const isTaxIncluded =
    taxCartDisplay.shoppingCartDisplayPrice === PriceDisplay.IncludingTax;

  const { items, totalQty } = cartData;
  const totalQuantity = useQtys ? totalQty : items.length;

  const numOfVisibleItems = displayMaxItems
    ? cartSummaryMaxItems
    : Math.min(cartSummaryMaxItems, DEFAULT_VISIBLE_ITEMS);

  const hasHiddenItems = items.length > numOfVisibleItems;
  const visibleItems = items.slice(0, numOfVisibleItems);

  const canDisplayMoreItems =
    hasHiddenItems &&
    !displayMaxItems &&
    numOfVisibleItems != cartSummaryMaxItems;

  return {
    canDisplayMoreItems,
    hasHiddenItems,
    isLoading: false,
    isTaxIncluded,
    totalQuantity,
    visibleItems,
  };
}

export const CartSummary: Container<CartSummaryProps> = ({
  routeCart,
  ...props
}) => {
  const [displayMaxItems, setDisplayMaxItems] = useState<boolean>(false);

  const translations = useText({
    ViewAll: 'Checkout.CartSummary.viewAll',
    ViewMore: 'Checkout.CartSummary.viewMore',
  });

  const {
    canDisplayMoreItems,
    hasHiddenItems,
    isLoading,
    isTaxIncluded,
    visibleItems,
    totalQuantity,
  } = useCartSummary(displayMaxItems);

  const viewMaxItems = useCallback(() => {
    setDisplayMaxItems(true);
  }, []);

  const viewAllItems = useCallback(() => {
    if (!routeCart) return;
    redirect(routeCart());
  }, [routeCart]);

  const action = useMemo(() => {
    if (!hasHiddenItems) return;

    if (canDisplayMoreItems) {
      return (
        <CartSummaryAction type="view-more" onClick={viewMaxItems}>
          {translations.ViewMore}
        </CartSummaryAction>
      );
    }

    if (routeCart) {
      return (
        <CartSummaryAction type="view-all" onClick={viewAllItems}>
          {translations.ViewAll}
        </CartSummaryAction>
      );
    }
  }, [
    hasHiddenItems,
    canDisplayMoreItems,
    viewMaxItems,
    translations.ViewMore,
    translations.ViewAll,
    viewAllItems,
    routeCart,
  ]);

  return (
    <CartSummaryComponent
      {...props}
      action={action}
      taxIncluded={isTaxIncluded}
      isLoading={isLoading}
      items={visibleItems}
      routeCart={routeCart}
      totalQuantity={totalQuantity}
    />
  );
};
